.layout-container {
  max-width: 1920px;
  min-width: 1200px;
  width: 100%;
  background: #eaebef; }
  .layout-container .banner-top {
    position: relative;
    width: 1200px;
    height: 1248px;
    margin: 0 auto; }
    .layout-container .banner-top a {
      position: absolute;
      left: 600px;
      bottom: 60px;
      display: block;
      width: 265px;
      height: 200px;
      animation: discount 3s infinite; }
  .layout-container .banner-bottom {
    position: relative;
    width: 1200px;
    height: 1140px;
    margin: 0 auto; }
    .layout-container .banner-bottom a {
      position: absolute;
      left: 500px;
      bottom: 115px;
      display: block;
      width: 230px;
      height: 63px;
      animation: discount 3s infinite; }
  .layout-container .rule {
    max-width: 1920px;
    min-width: 1200px;
    width: 100%;
    height: 250px;
    background-color: #a4d4f4; }
    .layout-container .rule dl {
      position: relative;
      width: 1200px;
      height: 320px;
      margin: 0 auto;
      color: #727171;
      background: url("../imgs/ali-cancer/ali-logo.png") no-repeat;
      background-size: 100px 93px;
      background-position: 920px 80px; }
      .layout-container .rule dl dt {
        padding-top: 40px;
        font-size: 17px;
        text-align: center;
        font-weight: bold;
        font-size: 22px; }
      .layout-container .rule dl dd {
        font-size: 14px;
        margin: 15px 0;
        text-align: center; }

@keyframes discount {
  0% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1); }
  50% {
    -webkit-transform: scale(0.7);
    -ms-transform: scale(0.7); }
  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1); } }
